@include ipad-landscape {

    .woocommerce-wishlist {

        table.wishlist_table {

            thead {

                tr {

                    th {
                        font-size: 18px;

                        &.product-price {
                            padding: 10px 18px 0 0;
                        }
                    }
                }
            }
        }
    }
}

@include ipad-portrait {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px 25px;
        }
    }
}

@include phone-landscape {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a {
                float: none;
                margin: 0 70% 15px 0;
            }
        }

        table.wishlist_table {

            thead {

                tr {

                    th {
                        @include mkdfH6();
                        padding-right: 11px;

                        &.product-thumbnail,
                        &.product-stock-status {
                            display: none;
                        }

                        &.product-add-to-cart {
                            padding: 0 40px;
                        }
                    }
                }
            }

            tbody {

                td {
                    font-size: 14px;
                }

                a.button:not(.yith-wcqv-button) {
                    font-size: 16px;
                    padding: 9px 11px;
                }

                tr {

                    td.product-name .yith-wcqv-button {
                        line-height: 10px;
                    }
                }
            }
        }
    }
}