/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .mkdf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .mkdf-woo-single-page {
        
        .mkdf-single-product-content {
            
            .images,
            .mkdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .mkdf-single-product-summary {
                margin: 68px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        margin: 17px 18px 9px;
                    }
                }
            }
        }
    }
}

@include ipad-portrait {

   .mkdf-woo-single-page {

       .woocommerce-tabs {

           ul.tabs {

               > li {
                   width: 100%;
                   background-color: #f7f7f5;

                   a {
                       margin: 0 30px 9px;
                   }

                   &:first-child {

                       a {
                           margin-top: 21px;
                       }
                   }
               }
           }

           .entry-content {
               padding-top: 30px;
           }
       }
   }
}

@include phone-landscape {
    
    .mkdf-woo-single-page {
	
	    &.mkdf-woo-single-thumb-on-left-side {
		
		    .mkdf-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .mkdf-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }
    }
}

@include phone-portrait {

    .mkdf-woo-single-page {

        .woocommerce-tabs {

            #reviews {

                ol.commentlist {

                    .comment_container {

                        img {
                            float: none;
                            margin-bottom: 5px;
                        }
                    }

                    .comment-text {
                        padding: 0;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */