/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */

@media only screen and (max-width: $phone-landscape) {

    .mkdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 33px 25px 23px;
            line-height: 1.67em;

            a {
            	float: none;
                margin: 0 70% 15px 0 !important;
                white-space: nowrap;

            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message Responsive styles - end
   ========================================================================== */