/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

	.mkdf-woocommerce-page {

		table.cart {

            .mkdf-quantity-buttons {
                width: 170px;
            }

            td.actions {

                .coupon {
                    top: -7px;
                }
            }
        }
	}
}

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {

			.mkdf-quantity-buttons {
				width: 125px;
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {

		table.cart {

			thead {

				tr {

					th {
						@include mkdfH6();
					}
				}
			}

			tr {

				th, td {
					font-size: 14px;
				}

				&.cart_item {

					td {
						padding: 15px 6px 15px 0;

						&.product-thumbnail {
							width: auto;
							padding-right: 0;

							img {
								display: none;
							}
						}

						&:last-child {
							padding-right: 0;
						}

						&:product-remove {
							padding-left: 0;
						}
					}
				}
			}

			td.actions {

				.coupon {
					margin: 0 0 20px;

					input[type="text"] {
						margin-right: 22px;
					}
				}

				.coupon,
				> input {
					float: none;
					display: block;
				}

				.button {
					float: none;
					margin: 0;
				}
			}

			.mkdf-quantity-buttons {
				width: auto;
			}
		}

		.cart-collaterals {

			.cart_totals {

				table {

					tr {

						th {
							@include mkdfH6();
						}
					}
				}

				span.woocommerce-Price-amount.amount {
					font-size: 14px;
				}

				tr.order-total {

					td {

						span {
							font-weight: 900;
						}
					}
				}
			}
		}
	}
}

@include phone-portrait {

	.mkdf-woocommerce-page {

		table.cart {

			tr.cart_item {

				td.product-remove {
					width: auto;
				}
			}

            thead tr th {
                padding: 10px 9px 10px 0;

                &.product-thumbnail {
                    padding: 0;
                }
            }

           .mkdf-quantity-buttons {

               .mkdf-quantity-input {
                   width: 35px;
               }

               .mkdf-quantity-plus,
               .mkdf-quantity-minus {
                   padding-right: 11px;
               }
           }
		}
	}
}

/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */